export default {
  data() {
    return {
      multiArray: [],
      multiIndex:[0,0],
      array:[
        {
          label:'无脊柱动物',
          id:0,
          children:[
            {
              id:1,
              label:'扁性动物1',
            },
            {
              id:2,
              label:'扁性动物2',
            }
          ]
        },
        {
          label:'XX动物',
          id:1,
          children:[
            {
              id:3,
              label:'XX动物2',
            },
            {
              id:4,
              label:'XX动物4',
            }
          ]
        }
      ],
      oneId:0,
      twoId:0,
    };
  },
  methods: {
    bindMultiPickerColumnChange(e){
      if(e.detail.column===0){
        this.initSelect(e.detail.value)
        this.multiIndex[0] = e.detail.value
      }else if(e.detail.column===1){
        this.multiIndex[1] = e.detail.value
        this.twoId = this.array[this.multiIndex[0]].children[this.multiIndex[1]].id
      }
      console.log(this.oneId)
      console.log(this.twoId)
    },
    initSelect(index){
      this.oneId = this.array[index].id
      this.multiIndex[1] = 0
      this.$set(this.multiArray,1,[])
      const arrTwo =  this.array[index].children.map(item => {　　　　// 此方法将校区名称区分到一个新数组中
        return item.label;
      });
      this.$set(this.multiArray,1,arrTwo)
      this.twoId = this.array[index].children[0].id
    },
    bindMultiPickerChange(e){},
    initData(){
      const arrOne = this.array.map(item => {　　　　// 此方法将校区名称区分到一个新数组中
        return item.label;
      });
      const arrTwo =  this.array[0].children.map(item => {　　　　// 此方法将校区名称区分到一个新数组中
        return item.label;
      });
       this.multiArray[0] =  arrOne
       this.multiArray[1] =  arrTwo
       this.oneId = this.array[0].id
       this.twoId = this.array[0].children[0].id
    },
  },
  mounted() {
    this.initData()
  },
};
